import 'swiper/css';
import axios from 'axios'
import React, { useState, useEffect } from 'react';

import SwiperMy from "../Components/SwiperMy";

export default function Items() {
    const [hotItems, setHotItems] = useState([]);
    const [noveltyItems, setNoveltyItems] = useState([]);
    const [discountItems, setDiscountItems] = useState([]);

    const fetchData = () => {
        axios.get('/hot_items')
            .then(response => {
                setHotItems(response.data);
            })
            .catch(error => {
                console.log(error);
            });

        axios.get('/discount_items')
            .then(response => {
                setDiscountItems(response.data);
            })
            .catch(error => {
                console.log(error);
            });

        axios.get('/novelty_items')
            .then(response => {
                setNoveltyItems(response.data);
            })
            .catch(error => {
                console.log(error);
            });
    };

    useEffect(() => {
        //    
        fetchData();

        //     5 
        const intervalId = setInterval(fetchData, 300000);

        //     
        return () => clearInterval(intervalId);
    }, []);

    return (
        <div>
            <h1 className='text-center mt-5'> </h1>
            <SwiperMy pageEn='/' pageRu='' category=" " data={hotItems} />
            <h1 className='text-center mt-5'></h1>
            <SwiperMy pageEn='/' pageRu='' category="" data={noveltyItems} />
            <h1 className='text-center mt-5'></h1>
            <SwiperMy pageEn='/' pageRu='' category="" data={discountItems} />
        </div>
    );
};
